body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@media (min-width: 600px) {
  body div.layout-component.drawer-open header, body div.layout-component.drawer-open .layout-content {
    margin-left: 320px;
    width: calc(100% - 320px); }
  body div.layout-component .layout-content {
    padding: 10px; } }

@media (max-width: 599px) {
  .quill-paper-wrapper {
    margin-top: 0px !important; }
  .ql-editor {
    max-height: calc(100vh - 227.5px) !important; } }

.gu-mirror.note-list-item {
  opacity: 1;
  box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 3px 1px -2px rgba(0, 0, 0, 0.12); }
  .gu-mirror.note-list-item:not(.list-btn-primary) {
    background-color: #424242 !important; }

.note-list-item .note-drag-handle {
  padding: 5px 0px;
  touch-action: none;
  position: absolute;
  color: white;
  right: 0px;
  cursor: -webkit-grab;
  cursor: grab;
  text-align: center;
  width: 30px;
  margin-right: 10px; }
  .note-list-item .note-drag-handle i {
    pointer-events: none; }

.Toastify__toast-body {
  text-align: center !important; }

.hidden {
  display: none !important; }

body {
  height: 100vh; }
  body div.layout-component header, body div.layout-component .layout-content {
    transition: margin-left 225ms cubic-bezier(0, 0, 0.2, 1) 0ms, width 225ms cubic-bezier(0, 0, 0.2, 1) 0ms;
    width: 100%; }
  body div.layout-component header .btn-logout {
    position: absolute;
    right: 5px; }

.list-btn-primary {
  background-color: #673ab7 !important; }
  .list-btn-primary:hover {
    background-color: #482880 !important; }

.list-btn-danger {
  background-color: #D32F2F !important; }
  .list-btn-danger:hover {
    background-color: #932020 !important; }

.flex-hbox {
  display: flex;
  flex-direction: column; }

.flex-item {
  flex: 1 1; }

.giant-progress-container-wrapper {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center; }
  .giant-progress-container-wrapper .giant-progress-container {
    text-align: center; }

div.drawer-paper {
  width: 320px; }

.note-list-component .search-input-wrapper {
  display: flex;
  padding-right: 10px;
  padding-left: 10px; }
  .note-list-component .search-input-wrapper .search-input {
    flex: 1 1; }
  .note-list-component .search-input-wrapper .search-icon {
    height: unset;
    margin-right: 8px; }

.note-list-component div .add-note-btn {
  background-color: #673ab7; }

.note-list-component .search-progress-complete {
  visibility: hidden; }

.note-list-component .list-of-notes .list-chk {
  padding: 0px; }

.note-list-component .list-of-notes .list-item-note-selected {
  background-color: #00e5ff; }
  .note-list-component .list-of-notes .list-item-note-selected .note-item-text-selected > span {
    color: rgba(0, 0, 0, 0.87);
    font-weight: 500; }

.note-editor-component .empty-editor-wrapper .empty-editor-paper {
  padding: 10px 15px;
  display: flex;
  margin: auto;
  margin-top: 25px;
  width: 300px; }
  .note-editor-component .empty-editor-wrapper .empty-editor-paper .backdrop-icon {
