    padding-bottom: 30px;
    border-color: transparent;
    color: white; }
    .note-editor-component .quill .ql-snow.ql-container .ql-editor {
      max-height: calc(100vh - 265px);
      min-height: 250px; }
      .note-editor-component .quill .ql-snow.ql-container .ql-editor.ql-blank::before {
        color: gray; }

.ql-snow .ql-picker.ql-expanded .ql-picker-label .ql-stroke {
  stroke: #4c4646 !important; }

.ql-snow .ql-picker.ql-expanded .ql-picker-label {
  color: #4c4646 !important; }

.note-list span.ui-draggable-dragging .note-list-item {
  z-index: 10;
  background-color: #555555; }

.note-list .note-list-item .note-list-item-icon {
  cursor: -webkit-grab;
  cursor: grab; }
  .note-list .note-list-item .note-list-item-icon * {
    pointer-events: none; }

.droppable-folder.ui-droppable-hover {
  -webkit-filter: contrast(50%);
          filter: contrast(50%); }

